import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Resolucao_ocorrencia } from '../../../modelo/Resolucao_ocorrencia'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'resolucao_ocorrencia-dados-edit',   templateUrl: 'resolucao_ocorrencia_dadosEdit.component.html' }) export class Resolucao_ocorrenciaDadosEditComponent implements OnInit {    resolucao_ocorrencia: Resolucao_ocorrencia;    private sub: Subscription;    private errorMessage: string;     constructor(private router: Router,               private route: ActivatedRoute,               private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.resolucao_ocorrencia.Resolucao_ocorrencia";                 let id = +params['id'];         this.resolucao_ocorrencia = new Resolucao_ocorrencia();         this.resolucao_ocorrencia.cd_resolucao_ocorrencia = id;         o.objetoJson = this.resolucao_ocorrencia;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a resolucao_ocorrencia. ';       }      this.resolucao_ocorrencia = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   }     onSubmit() {         }     onCancel(event: Event) {     event.preventDefault();     this.router.navigate(['/']);   }  }


